import { connect } from 'react-redux';
import fetchFilms from '../../redux/filmsSearch/filmsSearchOperations';
import Home from './Home';
import {
  addFilmToFavoritesActionCreator,
  markAsWatchedActionCreator,
  deleteFromFavorites,
  deleteFromWatched,
} from '../../redux/userList/userListActions';
import {
  setFilmQueryActionCreator,
  setActivePageActionCreator,
} from '../../redux/filmsSearch/filmsSearchActions';

const mapStateToProps = state => ({
  isLoading: state.isLoading,
  films: state.films,
  error: state.error,
  query: state.query,
  pages: state.pages,
  favoritesList: state.favoritesList,
  watchedList: state.watchedList,
});

const mapDispatchToProps = dispatch => ({
  fetchFilms: (query, page) => dispatch(fetchFilms(query, page)),
  addFilmToFavorites: film => dispatch(addFilmToFavoritesActionCreator(film)),
  markAsWatched: film => dispatch(markAsWatchedActionCreator(film)),
  deleteFromFavorites: id => dispatch(deleteFromFavorites(id)),
  deleteFromWatched: id => dispatch(deleteFromWatched(id)),
  setActivePage: page => dispatch(setActivePageActionCreator(page)),
  setFilmQuery: query => dispatch(setFilmQueryActionCreator(query)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Home);
