import actionTypes from '../actionTypes';

export const addFilmToFavoritesActionCreator = film => ({
  type: actionTypes.ADD_TO_FAVORITES,
  payload: {
    film,
  },
});

export const markAsWatchedActionCreator = film => ({
  type: actionTypes.MARK_WATCHED,
  payload: {
    film,
  },
});

export const deleteFromFavorites = id => ({
  type: actionTypes.DElETE_FROM_FAVORITES,
  payload: {
    id,
  },
});

export const deleteFromWatched = id => ({
  type: actionTypes.DELETE_FROM_WATCHED,
  payload: {
    id,
  },
});
