import actionTypes from '../actionTypes';

export const filmsLoadingActionCreator = () => ({
  type: actionTypes.FILM_LOADING,
});
export const filmsSuccessActionCreator = films => ({
  type: actionTypes.FILM_SEARCH_SUCCESS,
  payload: {
    films,
  },
});
export const setFilmQueryActionCreator = query => ({
  type: actionTypes.SET_FILM_QUERY,
  payload: {
    query,
  },
});
export const setPagesActionCreator = totalPages => ({
  type: actionTypes.SET_PAGES,
  payload: {
    totalPages,
  },
});
export const setActivePageActionCreator = activePage => ({
  type: actionTypes.SET_ACTIVE_PAGE,
  payload: {
    activePage,
  },
});
export const filmsErrorActionCreator = error => ({
  type: actionTypes.FILM_SEARCH_ERROR,
  payload: {
    error,
  },
});
