import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
// minified version is also included
// import 'react-toastify/dist/ReactToastify.min.css';
import styles from './Form.module.css';
import { ReactComponent as Search } from './search.svg';

const Form = ({ fetchFilms, query, setFilmQuery, setActivePage }) => {
  const [value, setValue] = useState(query);
  useEffect(() => {
    setActivePage(1);
    // eslint-disable-next-line
  }, [query]);
  const handleValueChange = e => {
    setValue(e.target.value);
  };
  const handleSubmit = e => {
    e.preventDefault();
    if (value === '') {
      toast('Nothing was entered!');
      return;
    }
    setFilmQuery(value);
    fetchFilms(value, 1);
  };

  return (
    <div>
      <h2 className={styles.title}> Home </h2>{' '}
      <form onSubmit={handleSubmit} className={styles.form}>
        <input
          // eslint-disable-next-line
    autoFocus type = "text"
          onChange={handleValueChange}
          value={value}
          placeholder="Enter film title here..."
          className={styles.input}
        />{' '}
        <button type="submit" className={styles.button}>
          <Search className={styles.search} />{' '}
        </button>{' '}
      </form>{' '}
      <p className={styles.censureMessage}>
        {' '}
        Results are passed through the censure filter and do not include adult
        content.{' '}
      </p>{' '}
      <ToastContainer />
    </div>
  );
};
Form.propTypes = {
  fetchFilms: PropTypes.func.isRequired,
  query: PropTypes.string.isRequired,
  setFilmQuery: PropTypes.func.isRequired,
  setActivePage: PropTypes.func.isRequired,
};
export default Form;
