import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import styles from './FilmItem.module.css';
import { ReactComponent as Heart } from '../heart.svg';
import { ReactComponent as Eye } from '../eye.svg';

const FilmItem = ({
  film,
  addFilmToFavorites,
  markAsWatched,
  favoritesList,
  watchedList,
  deleteFromFavorites,
  deleteFromWatched,
}) => {
  const addToFavorites = () => {
    addFilmToFavorites(film);
  };
  const addToWatched = () => {
    markAsWatched(film);
  };
  const deleteChosenFilmFromFavorites = () => {
    deleteFromFavorites(film.id);
  };
  const deleteChosenFilmFromWatched = () => {
    deleteFromWatched(film.id);
  };
  return (
    <div className={styles.film}>
      <NavLink to={`/${film.title}/${film.id}`}>
        <img
          className={styles.img}
          src={
            film.poster_path
              ? `https://image.tmdb.org/t/p/w200/${film.poster_path}`
              : 'https://www.viser.edu.rs/images/no_photo.jpeg'
          }
          alt="film poster"
        />
      </NavLink>

      <div>
        <h3 className={styles.title}> {film.title} </h3>

        {film.vote_average > 0 && (
          <p className={styles.vote}> {film.vote_average} </p>
        )}

        <p className={styles.overview}> {film.overview || 'no overview'} </p>

        <div className={styles.buttonWrapper}>
          {favoritesList.find(
            chosenFilm => Number(chosenFilm.id) === Number(film.id),
          ) ? (
            <button
              type="button"
              onClick={deleteChosenFilmFromFavorites}
              className={styles.button}
              title="delete from favorites"
            >
              <Heart className={styles.deleteFavButton} />
            </button>
          ) : (
            <button
              type="button"
              onClick={addToFavorites}
              title="add to favorites"
              className={styles.button}
            >
              <Heart className={styles.favButton} />
            </button>
          )}

          {watchedList.find(
            chosenFilm => Number(chosenFilm.id) === Number(film.id),
          ) ? (
            <button
              type="button"
              onClick={deleteChosenFilmFromWatched}
              className={styles.button}
              title="delete from watched"
            >
              <Eye className={styles.deleteWatchedButton} />
            </button>
          ) : (
            <button
              type="button"
              onClick={addToWatched}
              title="add to watched"
              className={styles.button}
            >
              <Eye className={styles.watchedButton} />
            </button>
          )}
        </div>
      </div>

      <button type="button" className={styles.showMoreButton}>
        <NavLink
          to={`/${film.title}/${film.id}`}
          className={styles.showMoreLink}
        >
          Show more
        </NavLink>
      </button>
    </div>
  );
};
FilmItem.propTypes = {
  film: PropTypes.shape({
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
    overview: PropTypes.string.isRequired,
    vote_average: PropTypes.number.isRequired,
    poster_path: PropTypes.string,
  }).isRequired,
  favoritesList: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  watchedList: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  deleteFromFavorites: PropTypes.func.isRequired,
  deleteFromWatched: PropTypes.func.isRequired,
  addFilmToFavorites: PropTypes.func.isRequired,
  markAsWatched: PropTypes.func.isRequired,
};

export default FilmItem;
