import React, { useEffect } from 'react';
import { useStateWithCallbackInstant } from 'use-state-with-callback';
import PropTypes from 'prop-types';
import styles from './Pagination.module.css';
import { ReactComponent as Arrow } from './arrow.svg';
import { ReactComponent as DoubleArrow } from './doubleArrow.svg';

const Pagination = ({ setActivePage, pages, query, fetchFilms }) => {
  const [activePage, setPageForState] = useStateWithCallbackInstant(1, () => {
    console.log('callback');
  });
  useEffect(() => {
    setPageForState(1);
    // eslint-disable-next-line
  }, [query]);

  const handleFirstLastPaginationClick = page => {
    setPageForState(page);
    setActivePage(page);
    fetchFilms(query, page);
    window.scrollTo({
      top: 0,
    });
  };

  const handlePaginationClick = type => {
    if (type === 'back') {
      setActivePage(activePage - 1);
      setPageForState(activePage - 1);
      fetchFilms(query, activePage - 1);
    } else {
      setActivePage(activePage + 1);
      setPageForState(activePage + 1);
      fetchFilms(query, activePage + 1);
    }
    window.scrollTo({
      top: 0,
    });
  };

  return (
    <div className={styles.pagination}>
      <button
        type="button"
        className={styles.button}
        disabled={activePage < 2}
        onClick={() => handleFirstLastPaginationClick(1)}
      >
        <DoubleArrow className={styles.firstArrow} />{' '}
      </button>{' '}
      <button
        type="button"
        className={styles.button}
        disabled={activePage < 2}
        onClick={() => handlePaginationClick('back')}
      >
        <Arrow className={styles.backArrow} />{' '}
      </button>{' '}
      <div className={styles.page}> {activePage} </div>{' '}
      <button
        type="button"
        className={styles.button}
        disabled={activePage === pages.totalPages}
        onClick={() => handlePaginationClick('forward')}
      >
        <Arrow className={styles.arrow} />{' '}
      </button>{' '}
      <button
        type="button"
        className={styles.button}
        disabled={activePage === pages.totalPages}
        onClick={() => handleFirstLastPaginationClick(pages.totalPages)}
      >
        <DoubleArrow className={styles.doubleArrow} />{' '}
      </button>{' '}
    </div>
  );
};
Pagination.propTypes = {
  setActivePage: PropTypes.func.isRequired,
  pages: PropTypes.shape({
    totalPages: PropTypes.number.isRequired,
  }).isRequired,
  query: PropTypes.string.isRequired,
  fetchFilms: PropTypes.func.isRequired,
};
export default Pagination;
